









	import {
	    mapMutations
	} from 'vuex'
	export default {
    data() {
        return {
			placeholder: '请输入内容',
			text: '',
			key: '',
		}
    },
	onLoad(option) {
		this.placeholder = option.placeholder;
		this.text = option.text;
		this.key = option.key;
	},
	
    methods: {
		...mapMutations(['setInputPageText']),
        saveText() {
			let obj = {
				text: this.text,
				key: this.key
			}
			this.setInputPageText(obj)
			
			uni.navigateBack({
				delta: 1
			})
		},
		descInput(e) {
			this.text = e.detail.value;
		}
    }
}
